:root {
    --borderColor: 0;
}
.messagesContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 9fr;
    grid-template-areas:
        "AZ AZ"
        "AX AC";
   
}
.mainText {
    text-align: center;
    font-size: 50px;
    grid-area: AZ;
    border: 3px solid;
    max-height:700px;
}

.inputUnit {
    grid-area: AX;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px solid;
}
.inputField {
    height: 3%;
    width: 98.5%;
    border: 0px solid #000000;
    font-size: 100%;
    font-weight:bold;
    border-radius: 10px;
    
    
    
}

.inputField:hover {
    border: 2px solid var(--borderColor);
}

    .inputField:focus {
        outline-color: var(--borderColor);
      
    }


.outputUnit {
    overflow: auto;
    grid-area: AC;
    border: 3px solid;
    max-height: auto;
    max-width: auto;
    background-color: yellow;
}
.uiMessageArr {
    max-height: 700px;
}