.masterhead {
    grid-area: Area1;
    display: grid;
    grid-template-areas:
        "Area20 Area21";
    background-color: red;
    justify-content: space-between;
    align-items:center;
    color: black;
    
}

.buttonHome {
    grid-area: Area20;
    padding-left: 5vh;
}

.buttonProfile {
    grid-area: Area21;
    padding-right: 5vh;
    color: black;
}
.text {
    color: black;
    font-size: 20px;
    border: 2.5px solid;
    text-decoration: none;
    border-radius: 10px;
}
.active {
    color: blue;
    font-size: 20px;
    border: 2.5px solid;
    text-decoration: none;
    border-radius: 10px;
}